body{
    background-image: linear-gradient(to left top, #4174c0, #486ac8, #5b5dcd, #744acb, #8f2dc4);
}
.TodooApp{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 50px;
    grid-row-gap: 0px;
}
.TodooApp > div{
    position: relative;
    display: block;
    width: 70%;
    margin: auto;
    border-radius: 25px;
    margin-top: 3rem;
    padding: 3rem 4rem;
    height: 700px;
    overflow-x: hidden;
    overflow-y: scroll;
    background-color: #2b2b2b;

    box-shadow: 10px 8px 37px -8px rgba(255,255,255,0.75);
    -webkit-box-shadow: 10px 8px 37px -8px rgba(255,255,255,0.75);
    -moz-box-shadow: 10px 8px 37px -8px rgba(255,255,255,0.75);
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#root{
  margin: 0 24px;
  position: relative;
  min-height: 100vh;
}
.TodoCounter{
    font-size: 1.8rem;
    text-align: center;
    font-weight: 400;
    color: #fff;
}
.TodoCounter span{
    font-weight: 700;
}
.TodoSearch{
    display: block;
    width: 100%;
}
.TodoSearch-input{
    width: 70%;
    margin: 40px auto;
    display: block;
    font-size: 1.4rem;
    padding: 4px 10px;
    border-radius: 10px;
}
.TodoItem{
    padding-bottom: 15px;
}
.TodoItem li{
    display: flex;
    background: #535353;
    padding: 10px 30px;
    border-radius: 15px;
    align-items: center;
}
.icon{
    font-size: 1.4rem;
}
.icon:hover{
    cursor: pointer;
}
.icon-check{
    padding-right: 20px;
    color: #757575;
}
.icon-check:hover{
    color: #fff;
}
.icon-check--active{
    color: #00C500;
}
.icon-delete{
    margin-left: auto;
    color: #DB332B;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}
.icon-delete:hover{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
}
.TodoItem-p{
    color: #fff;
    font-size: 1.5rem;
    margin: 0;
}
.TodoItem-p--complete{
    text-decoration: line-through;
    color: #a8a8a8;
}
@-webkit-keyframes honeycomb {

    0%,
    20%,
    80%,
    100% {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
    }

    30%,
    70% {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

@keyframes honeycomb {

    0%,
    20%,
    80%,
    100% {
        opacity: 0;
        -webkit-transform: scale(0);
        transform: scale(0);
    }

    30%,
    70% {
        opacity: 1;
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

.honeycomb {
    height: 24px;
    position: relative;
    width: 24px;
    display: block;
    margin:auto;
    padding-top: 40px;
}

.honeycomb div {
    -webkit-animation: honeycomb 2.1s infinite backwards;
    animation: honeycomb 2.1s infinite backwards;
    background: #f3f3f3;
    height: 12px;
    margin-top: 6px;
    position: absolute;
    width: 24px;
}

.honeycomb div:after,
.honeycomb div:before {
    content: '';
    border-left: 12px solid transparent;
    border-right: 12px solid transparent;
    position: absolute;
    left: 0;
    right: 0;
}

.honeycomb div:after {
    top: -6px;
    border-bottom: 6px solid #f3f3f3;
}

.honeycomb div:before {
    bottom: -6px;
    border-top: 6px solid #f3f3f3;
}

.honeycomb div:nth-child(1) {
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
    left: -28px;
    top: 0;
}

.honeycomb div:nth-child(2) {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
    left: -14px;
    top: 22px;
}

.honeycomb div:nth-child(3) {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
    left: 14px;
    top: 22px;
}

.honeycomb div:nth-child(4) {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
    left: 28px;
    top: 0;
}

.honeycomb div:nth-child(5) {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s;
    left: 14px;
    top: -22px;
}

.honeycomb div:nth-child(6) {
    -webkit-animation-delay: 0.5s;
    animation-delay: 0.5s;
    left: -14px;
    top: -22px;
}

.honeycomb div:nth-child(7) {
    -webkit-animation-delay: 0.6s;
    animation-delay: 0.6s;
    left: 0;
    top: 0;
}


.CreateTodoButton {
    position: -webkit-sticky;
    position: sticky;
    bottom: 0;
    left: 100%;
    font-size: 4rem;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    border: 0;
    color: #fff;
    background: #8B31CC;
    z-index: 1;
}
.CreateTodoButton:hover {
    cursor: pointer;
    -webkit-animation: rotation 0.5s linear;
            animation: rotation 0.5s linear;
}

@-webkit-keyframes rotation {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
    }
}

@keyframes rotation {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
    }
}
.ModalBackground{
    background-color: rgba(4, 23, 75, 0.61);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    border-radius: 25px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.ModalTodosBackground{
    background-color: rgba(4, 23, 75, 0.61);
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    border-radius: 25px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
.ModalTodosBackground img{
    max-width: 500px;
}
form{
    display: block;
    width: 90%;
    max-width: 450px;
}
label{
    margin: 20px 0;
    width: 100%;
    display: block;
    font-size: 2rem;
    text-align: center;
    font-weight: bold;
}
textarea{
    background-color: #e9e9e9;
    border: #6d5dff;
    border-radius: 5px;
    box-shadow: 4px 11px 35px 1px rgba(32,35,41,0.51);
    -webkit-box-shadow: 4px 11px 35px 1px rgba(32,35,41,0.51);
    -moz-box-shadow: 4px 11px 35px 1px rgba(32,35,41,0.51);
    font-size: 1.5rem;
    text-align: center;
    padding: 15px;
    height: 100px;
    width: calc(100%-25px);
    margin: 20px auto;
    display: block;
    
}
.TodoForm-buttonContainer{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
}
.TodoForm-button{
    width: 120px;
    cursor: pointer;
    display: inline-block;
    font-size: 1.3rem;
    color: #202329;
    padding: 10px;
    border-radius: 10px;
    border: none;
}
.TodoForm-button--cancel{
    background: rgb(135 248 255 / 38%);
    color:#e9e9e9;
}
.TodoForm-button--add{
    background: #61dafa;
    box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}
